.shine {
  animation: shimmer 1.5s infinite;
  background: linear-gradient(
    to right,
    #f6f7f8 0%,
    #edeef1 20%,
    #f6f7f8 40%,
    #f6f7f8 100%
  );
  background-size: 1000px 100%;
}

@keyframes shimmer {
  0% {
    background-position: -500px 0;
  }
  100% {
    background-position: 500px 0;
  }
}

.renderAuthorsShimmer {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 40px;
  border: none;
  display: flex;
  align-items: center;
}

.authorName {
  border-radius: 3px;
  height: 15%;
  width: 100%;
}
